::root {
}

@font-face {
  font-family: Calibre;
  src: url(CalibreRegular.otf);
}

body {
  --color-1: #4ddee0;
  --color-2: #000000;
  --color-3: #b3ac8a;
  --color-4: white;
  --fs-xxs: 12px;
  --fs-xs: 13px;
  --fs-sm: 14px;
  --fs-md: 16px;
  --fs-lg: 18px;
  --fs-xl: 20px;
  --fs-xxl: 22px;
  margin:  0;
  background-color: var(--color-1);
  --font-sans-serif-main: Calibre, Sans-Serif;
  --font-sans-serif-second: Roboto, Sans-Serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: scroll;
  min-height: 100vh;
}

#App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;

}

#root {
  min-height: 100vh;
}

.container {
  max-width: 1000px;
  margin: auto;
}

/*START FONT SIZE SECTION*/

.text {
  font-family: var(--font-sans-serif-second);
  font-size: clamp(var(--fs-sm), 4vw, var(--fs-lg));
  color: var(--color-4);
  text-decoration-color: var(--color-4);
  line-height: clamp(20px, 4vw, 30px);
  text-decoration: none;
}

.header1 {
  font-family: var(--font-sans-serif-main);
  font-weight: 600;
  font-size: clamp(40px, 8vw, 80px);
  color: var(--color-4);
  text-decoration-color: var(--color-4);
  text-decoration: none;
}

.header2 {
  font-family: var(--font-sans-serif-main);
  font-weight: 600;
  font-size: clamp(40px, 8vw, 80px);
  color: var(--color-3);
  text-decoration-color: var(--color-3);
  text-decoration: none;
}

.subtitle {
  font-family: var(--font-sans-serif-main);
  font-weight: 600;
  font-size: clamp(var(--fs-xxl), 6vw, 40px);
  color: var(--color-3);
  text-decoration-color: var(--color-3);
  line-height: clamp(30px, 8vw, 60px);
  text-decoration: none;
}

.subheader {
  font-family: var(--font-sans-serif-main);
  /*font-weight: 600;*/
  font-size: clamp(var(--fs-xl), 6vw, var(--fs-xxl));
  color: var(--color-3);
  text-decoration-color: var(--color-3);
  text-decoration: none;
}

.underline {
  border-bottom: 3px solid var(--color-1);
  padding-bottom: 20px;
  margin-bottom: 20px;
}

.teal {
  color: var(--color-1);
  text-decoration-color: var(--color-1);
}

.black {
  color: var(--color-2);
  text-decoration-color: var(--color-2);
}

.tan {
  color: var(--color-3);
  text-decoration-color: var(--color-3);
}

.white {
  color: var(--color-4);
  text-decoration-color: var(--color-4);
}

.Profile-text-box {
  padding-top: 0;
  margin-top: 30px;
  text-align: left;
}

.project-page-inner h5 {
  font-size: max(min(3.5vw, 30px),15px);
  margin-top: 20px;
  text-align: justify;
  font-style: italic;
  color: var(--color-1);
  text-decoration-color: var(--color-1);
}

.navbar-header {
  background-color: var(--color-2);
  width: 100px;
  overflow: hidden;
  user-select: none;

  opacity: 1;

  transition: opacity 0.5s ease;
}

.work-card-title {
  color: var(--color-1);
  font-size: calc(15px + 0.8vw);
}
/*END FONT SIZE SECTION*/

#App {
  text-align: center;
}

.navbar {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0.3125rem 0.5rem;
  background-color: var(--color-2);
  /*box-shadow: 0px 0px 50px 1px var(--color-2);*/
}

.navbar-menu {
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  display: inline-flex;
  padding: 0;
  margin: 0;
}

.menu-item {
  text-decoration: none;
  text-align: center;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  height: 70px;
}

.menu-text {
  color: var(--color-4);
  height: 70px;
  line-height: 70px;
}

.menu-text:hover {
  text-decoration: underline;
}

.navbar-header:hover {
  opacity: 0.7;
}

.projects-container {
  overflow: hidden;
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.work-card-container {
  display: inline-block;
  max-width: 30%;
  margin-top: 40px;
}

.work-card-image-container {
  border-radius: 20px;
  max-width: clamp(300px, 29%, 400px);
  aspect-ratio: 1 / 1;
  overflow:  hidden;
  transform:  scale(1);
  transition:  transform 0.5s ease 0s;
  user-select:  none;
  cursor: pointer;
  margin: auto;
  margin-bottom: 15px;
  margin-top: 15px;
}

.work-card-image-container::after {
  content: 'div';
  width: 1000px;
  height: 1000px;
  background-color: var(--color-1);
  color:  white;
  z-index: 99;
}

.work-card-image {
  width: 100%;
}

.work-card-image-container:hover {
  transform:  scale(1.05);
}

.animate-pop {
  animation-name: pop-up;
  -webkit-animation-name: pop-up;
  animation-timing-function: ease;
  -webkit-animation-timing-function: ease;
  animation-duration: 1s;
  -webkit-animation-duration: 1s;
}

.animate-slide-in-left {
  animation-name: slide-in-left;
  -webkit-animation-name: slide-in-left;
  animation-timing-function: ease;
  -webkit-animation-timing-function: ease;
  animation-duration: 1s;
  -webkit-animation-duration: 1s;
}

.animate-slide-in-bottom {
  animation-name: slide-in-bottom;
  -webkit-animation-name: slide-in-bottom;
  animation-timing-function: ease;
  -webkit-animation-timing-function: ease;
  animation-duration: 1.5s;
  -webkit-animation-duration: 1.5s;
}

@keyframes pop-up {
  0% {transform: scale(0)}
  85% {transform: scale(1.1)}
  100% {transform: scale(1)}
}

@keyframes slide-in-left {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slide-in-bottom {
  0% {
    transform: translateY(100vh);
    opacity: 0;
  }
  33% {
    transform: translateY(100vh);
    opacity: 0;
  }
  45% {
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.project-page-title {
  margin: 40px 0 40px 0;
  text-align: left;
}
.project-page-list-container {
  padding: 0;
  min-height: 30vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

ul {
  list-style: none;
}

.project-page-inner {
  opacity: 0;
  width: 30%;
  min-width: 300px;
  max-width: 500px;
  overflow: visible;
  display: block;
  position: absolute;
}

.caption {
  margin-top: 15px;
}

.sb-bignav {
  width: calc(15% - 80px);
  min-width: 70px;
  max-width: 170px;
  height: 30vw;
  min-height: 300px;
  max-height: 500px;
  overflow: visible;
  display: none;
  margin-right: 0;
  margin-left: 0;
  padding-right: 40px;
  padding-left: 40px;
  opacity: 0;
  z-index: 9;
  cursor: pointer;
  position: absolute;
  
  transition: opacity .2s;
  -moz-transition: opacity .2s;
  -webkit-transition: opacity .2s;

  color: white;
  font-size: 148pt;
  text-align: left;
  line-height: clamp(250px, 30vw - 50px, 450px);
  text-shadow: 0px 0px 15px rgb(119, 119, 119);
}

label[title="Next"] { 
  transform: translateX(clamp(150px, 15vw, 250px));
  /*transform: translateX(+15vw - 80px + min(-150px, -15vw));*/
  /*right: min(60%, 90% - 300px);*/
  text-align: right;
}

label[title="Previous"] {
  /*transform: translateX(min(-15vw, -150px));*/
}

#project-image-list {
  padding: 0;
  margin-bottom: 150px;
  width: clamp(300px, 30vw, 500px);
  display: block;
  /*min-height: max(40vw, 500px);*/
  height: clamp(300px, 30vw, 500px);
}
#project-image-list > ul {
  padding: 0;
  width: max(30vw, 300px);
  display: block;
  /*min-height: max(30vw, 300px);*/
}

.project-page-inner:hover ~ div > label.sb-bignav { opacity: 0.5; }

label.sb-bignav:hover { opacity: 1; }

.project-page-inner img {
  width: 100%;
  /*aspect-ratio: 1 / 1;*/
}

input:checked + .project-page-inner {
  opacity: 1;
  transition: opacity 1s ease-in-out;
  -moz-transition: opacity 1s ease-in-out;
  -webkit-transition: opacity 1s ease-in-out;
}

input:checked ~ div > label { display: block; }

.project-page-container input { display: none; }

.project-page-container {
  margin: 0 4vw 0 10vw;
}

.Project-Page-Heading {
  padding-top: 30px;
  padding-bottom: 30px;
  margin-bottom: 30px;
}

#project-image-list * {
    user-select: none;
    -ms-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -webkit-touch-callout: none;
}

.project-page-image-container {
  width: 100%;
  display: block;
}

.project-page-description-text {
  white-space: pre-line;
  text-align: left;
  /*top right bottom left*/
  margin: 0px 5vw 50px 5vw;
  min-width: calc(300px - 11vw);
  flex: 1;
}

.panel {
  background-color: var(--color-2);
  border: 10px solid var(--color-1);
  min-width: 300px;
  display: block;
  min-height: 100%;
  flex: 1;
}

.logo {
  width: 100%;
  height: 100%;
  display: block;
}

.logo img {
  width: 100%;
  margin: 0;
  /*max-height: 100px;*/

}

.menuicon {
  width: 70px;
  cursor: pointer;
  user-select: none;
}

.menu {
  position: fixed;
  margin-top: 200px;
  right: 0;
  z-index:  99;
  background-color: var(--color-2);
}

.skills-list {
  list-style: circle;
  display: flex;
  justify-content: space-evenly;
}

/*START SCREEN SIZE OVERRIDES*/
@media all and (max-width: 800px) {
  .project-page-description-text {
    min-width: 80%;
  }
  input:checked ~ div > label.sb-bignav { 
    opacity: 0.25;
    font-size: 80pt;
  }
  .logo {
    width: 75px;
    height: 75px;
  }
}

@media all and (max-width: 1300px) {
  .projects-container {
    flex-direction: column;
    align-items: center;
  }

  .project-page-container {
    margin: 0 4vw 0 4vw;
  }

  .menuicon {
    width: 35px;
  }

  .menu-item {
    height: 35px;
  }

  .menu-text {
    color: var(--color-4);
    height: 35px;
    line-height: 35px;
    font-size: 14px;
  }

}

/*END SCREEN SIZE OVERRIDES*/